.PlayGrid {
  border: 3px solid #d6d6d6;
  border-radius: 12px;
  font-size: 0;
  overflow: hidden;
}

.PlayGrid-row {
  border-bottom: 3px solid #d6d6d6;
}

.PlayGrid-row:last-child {
  border-bottom: none;
}

.PlayGrid-cell {
  position: relative;
  display: inline-block;
  width: 33.33%;
  padding-bottom: 33.33%;
  border-right: 3px solid #d6d6d6;
  box-sizing: border-box;
}

.PlayGrid-cell:last-child {
  border-right: none;
}

.IconCircle {
  display: inline-block;
  width: 100%;
  height: 100%;
  fill: currentColor;
  vertical-align: top;
}

.IconCross {
  display: inline-block;
  width: 100%;
  height: 100%;
  fill: currentColor;
  vertical-align: top;
}

.PlayGrid_Button {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  cursor: pointer;
  transition: background-color 0.2s ease-out;
}

.PlayGrid_Button:hover {
  background-color: #f5f5f5;
}

.PlayGrid_Button:focus {
  background-color: #f5f5f5;
}

.PlayGrid_Button:active {
}

.PlayGrid_Button:disabled {
  cursor: default;
}

.PlayGrid_Button-icon {
  display: inline-block;
  width: 75%;
  height: 75%;
}

/* @media (min-width: 360px) {
  .PlayGrid_Button-icon {
    width: 72px;
    height: 72px;
  }
} */

.PlayerPanel {
  width: 100%;
  text-align: center;
  font-size: 0;
  margin-bottom: 32px;
  color: #808080;
}

.PlayerPanel-title {
  text-align: center;
  margin: 0;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 100;
}

.PlayerPanel-icon {
  display: inline-block;
  width: 20%;
}

.PlayerPanel-separator {
  margin: 0 auto;
  margin-bottom: 4px;
  width: 50%;
  height: 4px;
  border-radius: 2px;
  background-color: #808080;
}

.PlayerPanel--active .PlayerPanel-separator {
  background-color: #5eacae;
}

.IconReload {
  display: inline-block;
  width: 100%;
  vertical-align: top;
  fill: currentColor;
}

.ButtonIcon {
  display: inline-block;
  margin: 0;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  transition: all 0.2s ease-out;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

@media (min-width: 500px) {
  .ButtonIcon {
    padding: 16px;
  }
}

/* .ButtonIcon:focus {
  box-shadow: 0 0 8px 0 #575757;
}

.ButtonIcon:hover {
  background-color: #e3e3e3;
} */

.ButtonIcon:active {
  background-color: #ccc;
}

.ButtonIcon-holder {
  display: inline-block;
  width: 22px;
  height: 22px;
}

@media (min-width: 500px) {
  .ButtonIcon-holder {
    width: 28px;
    height: 28px;
  }
}

.ButtonIcon--size-normal {
}

.ButtonIcon--size-small {
  padding: 4px;
  border-radius: 6px;
}

.ButtonIcon--size-small .ButtonIcon-holder {
  width: 16px;
  height: 16px;
}
