} */

.IconInfo {
  display: inline-block;
  width: 100%;
  vertical-align: top;
  fill: currentColor;
}

.DialogWindow {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.DialogWindow-container {
  height: 100vh;
  padding: 0 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.DialogWindow--visible {
  display: block;
}

.DialogWindow-content {
  position: relative;
}

.DialogWindow-iconButtom {
  position: absolute;
  top: 8px;
  right: 8px;
}

.RulesPanel {
  text-align: left;
  padding: 16px;
  background-color: #fff;
  color: #000;
}

.RulesPanel-title {
  text-align: center;
  margin: 0;
  margin-bottom: 16px;
}

.RulesPanel-content {
  margin: 0;
}

.SummaryPanel {
  text-align: center;
  padding: 48px;
  background-color: #fff;
}

.RulesPanel-title {
  font-size: 24px;
  margin-bottom: 32px;
}

.Button {
  display: inline-block;
  margin: 0;
  padding: 16px 24px;
  border: none;
  border-radius: 14px;
  line-height: 20px;
  font-size: 24px;
  background-color: #2196f3;
  color: #fff;
  box-sizing: border-box;
  cursor: pointer;
  outline: none;
  transition: background-color 0.2s ease-out;
  font-family: inherit;
}

.Button:focus {
  box-shadow: 0 0 8px 0 #1976d2;
}

.Button:hover {
  background-color: #1976d2;
}

.Button:active {
  background-color: #13589d;
}

.PlayGridApp {
  position: relative;
  text-align: center;
  font-family: inherit;
}

.PlayGridApp-title {
  margin: 0;
  font-size: 42px;
  margin-bottom: 48px;
}

.PlayGridApp-playerPanel {
  margin-bottom: 16px;
}

.PlayGridApp-flexWrapper {
  display: -webkit-flex;
  display: flex;
  margin: -16px;
}

.PlayGridApp-playerPanelItem {
  padding: 16px;
}

.PlayGridApp-iconButtonPanel {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 8px;
}

.Container {
  margin: 0 auto;
  padding: 16px;
  max-width: 640px;
  box-sizing: border-box;
}

